html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif,
    'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';

  margin: 30px;
  padding: 0;
  text-align: center;
  background-color: #7a8688;
}

.gallery {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px; 
  padding: 0;
 
}

.stats {
  display: flex;
  justify-content: space-around;
}

.card-item {
  width: 100%;
  height: 100%;
  position: relative;
}

.image-wrp {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.image-wrp__image {
  width: 100%;
  height: 370px;
  object-fit: cover;
  object-position: 50% 50%;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.card-item:hover .image-wrp__image {
  transform: scale(1.05);
}

.search-form {
  display: flex;
  justify-content: center;
}

.search-form__input {
  border: 1px solid #000;
  width: 232px;
  height: 40px;
  font-style: italic;
  line-height: 1.6;
  padding-left: 10px;
  font-size: 20px;
}

button {
  background-color: transparent;
  border: 1px solid #000;
  font-size: 20px;
  color: #000;
  font-family: sans-serif;
  text-transform: uppercase;
  cursor: pointer;
}

//spinner
.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

.is-hidden {
  display: none;
}